#side-by-side-sign {
    display: inline-block;
    width: 10px;
    font-weight: 800;
    font-family: Helvetica Neue, monospace;
    cursor: pointer;
}
#side-by-side {
    display: inline-block;
    width: 15px;
    font-weight: 400;
  	font-family: Helvetica Neue, monospace;
    cursor: pointer;
}
/* unvisited link */
#side-by-side:link {
  color: rgb(210, 210, 210);
  text-decoration: none;
}
/* visited link */
#side-by-side:visited {
  color: rgb(210, 210, 210);
  text-decoration: none;
}
/* mouse over link */
#side-by-side:hover {
  color: rgb(210, 210, 210);
  text-decoration: none;
}
/* selected link */
#side-by-side:active {
  color: rgb(210, 210, 210);
  text-decoration: none;
}
