#picture {
  transform: translate(-50%, -50%);
  top: 52%;
  left: 50%;
  position: absolute;
  max-height:22cm;
  max-width:42cm;
  width: 100%;
  height: 100%;
}
path, circle {
  transition: all 500ms ease;
}

#picture {
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    position: absolute;
}
path, circle {
  transition: all 500ms ease;
}

